<template>
  <div class="subscribe-page">
    <!-- 订阅成功后显示内容 -->
    <success-message :title="$t('login.subscribe.subscribe_success_title')" v-if="completed">
      {{ $t('login.subscribe.subscribe_success') }}
    </success-message>

    <div v-else>
      <h1 class="subscribe-title">{{ $t('login.sign_up_for_updates_cap') }}</h1>

      <form-subscribe @validate="submit" ref="formSubscribe" />

      <el-button
        type="primary"
        @click="$refs.formSubscribe.validateForm()">{{ $t('login.confirm_subscribe') }}</el-button>

      <back-login style="margin: 15px 0 30px 160px;" />
    </div>
  </div>
</template>

<script>
import mixinLoading from '@/mixins/loading';
import { subscribeOurWebsite } from '@/api/account';
import SuccessMessage from '../components/SuccessMessage';
import BackLogin from '../components/BackLogin';
import FormSubscribe from './components/FormSubscribe';


export default {
  components: {
    SuccessMessage,
    FormSubscribe,
    BackLogin,
  },
  mixins: [mixinLoading],
  data() {
    return {
      completed: false,
    };
  },
  methods: {
    async submit(form) {
      // 当发送请求前，添加 Loading 状态
      this.startLoading();

      try {
        await subscribeOurWebsite({ ...form, email: form.email.toLowerCase() });
        this.completed = true;
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.endLoading();
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.subscribe-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 460px;
  height: 100%;
  margin-right: 50px;

  .subscribe-title {
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    color: rgba(51, 51, 51, 1);
    margin: 0 0 20px 108px;
  }
  .el-button {
    width: calc(100% - 160px);
    margin-left: 160px;
  }
}
</style>
