<template>
  <el-select
    v-model="selectedValue"
    :placeholder="placeholder"
    :disabled="disabled"
    clearable
    @change="handleChange">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="$i18n.i18next.language === 'en' ?
        (item.usName || item.name) : (item.name || item.usName)"
      :value="item.value" />
  </el-select>
</template>

<script>
import { getDictByType } from '@/loaders/dictionary';

export default {
  props: {
    // 选中的值
    value: {
      type: [String, Number],
      default: '',
    },
    // 字典类型
    type: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      selectedValue: '',
      options: [],
    };
  },
  async created() {
    try {
      this.options = (await getDictByType(this.type));
    } catch (err) {
      this.$message.error(err.message);
    }
  },
  watch: {
    value: {
      handler(val) {
        this.selectedValue = val;
      },
      immediate: true,
    },
  },
  methods: {
    handleChange() {
      const selectedOptions = this.options.find(item => item.value === this.selectedValue);
      this.$emit('on-change', this.selectedValue, selectedOptions);
    },
  },

};
</script>
