<template>
  <div class="success-message">
    <!-- 标题 -->
    <p class="tip-success">
      <!-- <i class="iconfont icon-tick-circle-o" /> -->
      <img class="image" src="@/assets/img/public/success.png" />
      <span class="text">{{ title }}</span>
    </p>

    <!-- 文字描述 -->
    <p class="message">
      <slot />
    </p>

    <!-- 倒计时提示语言 -->
    <!-- <p class="tip-back">
      {{ $t('login.index.tip_back', { num: accountDownNum }) }}
    </p> -->

    <div class="text-align-right">
      <!-- 回到登录页按钮 -->
      <el-button type="primary" @click="jumpToLogin">
        {{ countDown > 0 ? `${accountDownNum}s ${$t('public.back')}` : $t('public.back') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';

export default {
  props: {
    title: {
      type: String,
      default: i18n.t('login.index.successfully'),
    },
    countDown: {
      type: Number,
      default: 15,
    },
  },
  data() {
    return {
      accountDownNum: this.countDown,
      timer: null,
    };
  },
  mounted() {
    if (this.countDown > 0) {
      this.timer = setInterval(() => {
        this.accountDownNum--;
        if (!this.accountDownNum) {
          clearInterval(this.timer);
          this.$router.push({ name: 'Login' });
        }
      }, 1000);
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
    this.timer = null;
  },
  methods: {
    jumpToLogin() {
      clearInterval(this.timer);
      this.$router.push({ name: 'Login' });
    },
  },
};
</script>

